<template>
  <div id="address-index">
		<common-header :title='title'></common-header>
		
    <div class="address-list" v-for="(item,index) in addressList">
      <div class="list-item" @click="selectAddress(item,index)" :data-id="item.id">
        <div class="item-info">
          <span class="info-name fl-left text-over">{{item.name}}</span>
          <span class="fl-right">{{item.mobile}}</span>
        </div>
        <div class="item-address">{{item.street}}</div>
      </div>
      <div class="list-btn ">
        <div class="check-box fl-left">
          <div>
            <input type="radio" name="address"  :checked="activeIndex!='' || activeIndex === 0?(activeIndex==index?'checked':''):(item.selected==1?'checked':'')" @click="radioClick(item,index)"/>
          </div>
        </div>
        <span class="fl-left" v-show="activeIndex!='' || activeIndex === 0?(activeIndex==index):(item.selected==1)" :class="{ 'c-de7fef': activeIndex!='' || activeIndex === 0?(activeIndex==index):(item.selected==1)}">默认地址</span>
        <span class="fl-left" v-show="activeIndex!='' || activeIndex === 0?(activeIndex!=index):(item.selected==0)">设为默认</span>
				
        <div class="fl-right btn-item" style="margin-left: 0.36rem" @click="delAddress(item)">
          <img class="icon" src="../../assets/cart/common_btn_del@3x.png">
          <span>删除</span>
        </div>
        <div class="fl-right" @click="toEdit(item)">
          <img class="icon" src="../../assets/cart/common_btn_compile@3x.png">
          <span>编辑</span>
        </div>
      </div>
    </div>
  
  	<div class="foot-btn" @click="linkTo('AddressManage')">添加新地址</div>
  </div>
</template>

<script>
	import CommonHeader from '@/components/Header/CommonHeader'
	
  export default {
    data () {
      return {
      	title:'地址管理',
      	activeIndex:'',
        isDefault: false,
        addressList:[
//      	{id:1,name:'联系人1',mobile:13800000000,regionId:440100,street:'详细地址详细地址详细地址详细地址详细地址详细地址详细地址详细地址详细地址详细地址',selected:0},
//      	{id:2,name:'联系人2',mobile:13800000001,regionId:440100,street:'详细地址',selected:0},
//      	{id:3,name:'联系人3',mobile:13800000002,regionId:440100,street:'详细地址',selected:1}
        ],
      }
    },
    components: {
				CommonHeader
		},
    watch: {},
    created(){
    	this.getShippingAddressList()
    },
    methods: {
      //设置默认地址
      setDefault(e){
//				this.isDefault = e.target.checked
      },
      //删除地址
      delAddress(item){
        console.log('删除地址')
        this.$popup({
          title: '删除地址',
          content:'是否确认要删除选中的地址？'
        }).then((res) => {
          console.log(item)
          if(res == 'sure'){
          	this.getShippingAddressSwitch(1,item.id)
          }
        })
      },
      toEdit(item){
      	this.$router.push({name:'AddressEdit', query:{item}})
      },
      radioClick(item,index){
      	this.activeIndex = index
      	Object.assign(item,{selected:1})
      	this.getShippingAddressSubmit(item);
      },
      selectAddress(item,index){
      	let path = this.$route.name
      	if(path == 'ConfirmAddress'){
      		localStorage.removeItem('confirmAddress')
      		localStorage.setItem('confirmAddress',JSON.stringify(item))
      		this.$router.go(-1)
      	}
      },
			getShippingAddressList(){
				this.$Api.getShippingAddressList().then((res)=>{
					console.log(res)
					if(res.q.s==0){
						this.addressList = res.q.addresses
					}
				})
			},
			getShippingAddressSubmit(params){
				console.log(params)
				this.$Api.getShippingAddressSubmit(params).then((res)=>{
					
				})
			},
			getShippingAddressSwitch(a,id){
				this.$Api.getShippingAddressSwitch(a,id).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
    				this.getShippingAddressList()
					}else{
						this.$toast(res.q.d)
					}
				})
			},
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #address-index{
    min-height: 100%;
    background: #f0f0f0;

    .address-list{
      width: 100%;
      padding-top: 0.4rem;
      background: 100%;
      background: #fff;
      margin-bottom: 0.27rem;
    }
    .list-item{
      padding: 0 0.4rem;
      border-bottom: 1px #ccc solid;
    }
    .item-info{
      height: 0.8rem;
      line-height: 0.8rem;
      font-size: 0.43rem;
      color: #1a1a1a;
      .info-name{
        display: block;
        width: 6rem;

      }
    }
    .item-address{
      font-size: 0.37rem;
      padding: 0.25rem 0;
    }
    .list-btn{
      width: 100%;
      height: 1.17rem;
      line-height: 1.17rem;
      padding: 0 0.4rem;
      font-size: 0.37rem;
      img{
        width: 0.53rem;
        vertical-align: text-top;
      }
    }
    .c-de7fef{
      color: #de7fef
    }
    .check-box {
      width: 0.8rem;
      height: 1.17rem;
      position: relative;

      .input-checkbox{
        position: absolute;
        top: 50%;
        left: 0;
        margin-top: -0.28rem;
        width: 0.53rem;
        height: 0.53rem;
        /*border: solid 1px #999999;
        border-radius: 100%;*/
        input{
          width: 100%;
          height: 100%;
          /*filter:alpha(opacity=0);
          -moz-opacity:0;
          -khtml-opacity: 0;
          opacity: 0;*/
          position: absolute;
          top: 0;
          left: 0;
        }
      }
      .input-checked{
        width: 0.53rem;
        height: 0.53rem;
        background: url("../../assets/cart/ico_checked.png") no-repeat center;
        background-size: 100% 100%;
        border: none;
      }
    }
    // input[type=radio]{
    // 	position: absolute;
    //   top: 50%;
    //   left: 0;
    //   transform: translateY(-50%);
	  //   width: 0.53rem;
	  //   height: 0.53rem;
	  //   border: solid 1px #999999;
	  //   border-radius: 100%
    // }
		// input[type=radio]:checked:before{
		// 	display: inline-block;
		// 	content: "";
		// 	position: absolute;
		// 	left: 0;
		// 	top: 0;
		// 	width: 0.53rem;
    //   height: 0.53rem;
    //   background: url("../../assets/cart/ico_checked.png") no-repeat center;
    //   background-size: 100%;
    //   border: none;
    // }
    input[type=radio]{
    	position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
	    width: 0.53rem;
	    height: 0.53rem;
      -moz-appearance: none;
      -webkit-appearance: none;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 0.533rem;
        height: 0.533rem;
        background: #fff;
        border: 1px solid #eee;
        border-radius: 50%;
        z-index: 9;
        box-sizing: border-box;
      }

      &:checked {
        &::after {
          border: none;
          background: url("../../assets/cart/ico_checked.png") no-repeat center;
          background-size: 100%;
        }
      }
    }
    .foot-btn{
      position: fixed;
      bottom: 0;
      font-size: 0.45rem;
      width: 100%;
      height: 1.31rem;
      line-height: 1.31rem;
      @include bg-color;
      text-align: center;
      color: #fff;
    }
  }
</style>



